.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1em;
}

.logo-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 1em;
}

.actions-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 1em;
}

.history-element {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  font-size: 1em;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  user-select: none;
}

.history-element:hover {
  background-color: #eeeeee;
  cursor: pointer;
}

.geometry-element {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  font-size: 1em;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  user-select: none;
}

.options-form {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  font-size: 1em;
  padding: 5px;
}

.options-form-element {
  display: flex;
  flex: 1;
  width: 100%;
  font-size: 1em;
  padding: 10px;
}

.geometry-element:hover {
  background-color: #eeeeee;
  cursor: pointer;
}



.header-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 1em;
  width: 100%
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}